.choice-button {
    background-color: #fff;
    border: 24px solid transparent;
    border-radius: 50%;
    width: 100%;
    height: 100%;
}

.choice-button[data-type="scissor"] {
    border-color: hsl(40, 84%, 53%);
    box-shadow: inset 0 8px 0px #BABED3, 0 8px 0px hsl(39, 89%, 49%);
}

.choice-button[data-type="rock"] {
    border-color: hsl(349, 70%, 56%);
    box-shadow: inset 0 8px 0px #BABED3, 0 8px 0px hsl(349, 71%, 52%);
}

.choice-button[data-type="paper"] {
    border-color: hsl(230, 89%, 65%);
    box-shadow: inset 0 8px 0px #BABED3, 0 8px 0px hsl(230, 89%, 62%);
}

@media (max-width:500px) {
    .choice-button {
        border: 15px solid transparent;

    }

        .choice-button[data-type="scissor"] {
            box-shadow: inset 0 6px 0px #BABED3, 0 6px 0px hsl(39, 89%, 49%);
        }
    
        .choice-button[data-type="rock"] {
            box-shadow: inset 0 6px 0px #BABED3, 0 6px 0px hsl(349, 71%, 52%);
        }
    
        .choice-button[data-type="paper"] {
            box-shadow: inset 0 6px 0px #BABED3, 0 6px 0px hsl(230, 89%, 62%);
        }
}