
.rule-modal-overlay {
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 10;
}
.rules-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 415px;
    padding: 35px;
    background-color: #fff;
    border-radius: 10px;
}
.rules-modal .modal-title {
    color: var(--dark-text);
}
.rules-modal .modal-title button {
    float: right;
    background-color: transparent;
}
.rules-modal .modal-title span {
    font-size: 1.6em;
   font-weight: 700;
}
.rules-modal .modal-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

@media (max-width:500px){
    .rules-modal {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    transform: translate(0,0);
    width: 100%;
    height: 100%;
    border-radius: 0px;
}
.rules-modal .modal-title {
   text-align: center;
    color: var(--dark-text);
    margin-top: 100px;
}

.rules-modal .modal-title button {
    float: none;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: transparent;
    margin-bottom: 67px;
}
.rules-modal .modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 305px;
}

}