.rules {
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 32px;

}

.rules button {
    padding: 10px 40px;
    color: #fff;
    background-color: transparent;
    border: 2px solid var(--header-outline);
    border-radius: 10px;
}

@media (max-width: 500px) {
    .rules {
        justify-content: center;
        bottom: 11px;
    }
}