.home {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 475px;
    min-height: 435px;
    /* background-color: pink; */
    background-image: url(../../assets/images//bg-triangle.svg);
    background-repeat: no-repeat;
    background-position: center;
}

.home .paper,
.home .scissor,
.home .rock {
    width: 200px;
    height: 197px;
}

.home .paper img,
.home .scissor img,
.home .rock img {
    width: 69px;
    height: 69px;
}

.home .paper {
    position: absolute;
    left: 0;
    top: 0;
}

.home .scissor {
    position: absolute;
    right: 0;
    top: 0;
}

.home .rock {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
}

@media (max-width:500px) {

    .home .paper,
    .home .scissor,
    .home .rock {
        width: 134px;
        height: 129px;
    }

    .home .paper img,
    .home .scissor img,
    .home .rock img {
        width: 50px;
        height: 50px;
    }
        .home {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            min-width: 313px;
            min-height: 278px;
            /* background-color: pink; */
            background-image: url(../../assets/images//bg-triangle.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 195px 173px;
        }
}