.game-board {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    gap: 70px;
    font-weight: 600;
}

.choice {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 67px;
    font-size: 1.4em;
    color: #fff;
}

.choice .image {
    width: 290px;
    height: 290px;
    border-radius: 50%;
}

.choice .image img {
    width: 80px;
    height: 80px;
}

.choice .image.active {
    position: relative;
    outline: 60px solid #293251;
}

.choice .image.active:after,
.choice .image.active::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    z-index: -1;

}

.choice .image.active:after {
    outline: 60px solid #232E4C;
    outline-offset: 59px;
}

.choice .image.active::before {
    outline: 60px solid #202B4B;
    outline-offset: 118px;
}

.choice .empty-choice {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #182341;
}

.result {
    align-self: flex-end;
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 27px;
}

.result span {
    text-align: center;
    font-size: 2.6em;
    font-weight: 700;
    letter-spacing: -2px;

}

.result button {
    font-weight: 700;
    padding: 20px 60px;
    color: var(--dark-text);
    border-radius: 10px;

}

.result button:hover {
    color: var(--rock-gradient-end);

}


@media (max-width:500px) {
    .game-board {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        display: flex;
        align-items: center;
        gap: 0px;
    }

    .choice {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        flex: 1;
        gap: 40px;
        font-size: 1em;
        color: #fff;
    }
    

    .choice .image {
        width: 134px;
        height: 134px;
        border-radius: 50%;
    }

    .choice .image img {
        width: 50px;
        height: 50px;
    }

    .choice .image.active {
        position: relative;
        outline: 17px solid #293251;
    }

    .choice .image.active:after,
    .choice .image.active::before {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        width: 100%;
        height: 100%;
        border-radius: 50%;
        z-index: -1;

    }

    .choice .image.active:after {
        outline: 30px solid #232E4C;
        outline-offset: 16px;
    }

    .choice .image.active::before {
        outline: 33px solid #202B4B;
        outline-offset: 45px;
    }

    .result {
        position: absolute;
        bottom: -180px;
        left: 50%;
        transform: translateX(-50%);
        margin-bottom: 0px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 27px;
    }

    .result span {
        text-align: center;
        font-size: 3em;
        font-weight: 700;
        letter-spacing: -2px;

    }

    .result button {
        padding: 15px 63px;        
        border-radius: 8px;

    }

    .result button:hover {
        color: var(--rock-gradient-end);

    }
}