.header-wrapper {
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 45px;
    z-index: 1;
}

.header-wrapper .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 704px;
    margin: 0 auto;
    padding: 17px;
    border: 3px solid var(--header-outline);
    border-radius: 15px;

}

.header .logo {
    margin-left: 12px;
}

.header .score {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 20px 50px;
    background-color: #fff;
    border-radius: 10px;
    
}

.header .score span:nth-child(1) {
    font-size: 1rem;
    font-weight: 600;
    color: var(--score-text);
}

.header .score span:nth-child(2) {
    font-size: 3.2rem;
    font-weight: 700;
    letter-spacing: -3px;
    color: var(--dark-text);
}

@media (max-width:500px) {
    .header-wrapper {       
        padding: 30px;
           
    }

    .header-wrapper .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 704px;
        margin: 0 auto;
        margin-top: 18px;
        padding: 17px;
        border: 3px solid var(--header-outline);
        border-radius: 10px;

    }

    .header .score {
        display: flex;
        flex-direction: column;
        padding: 8px 24px;
        background-color: #fff;
        border-radius: 5px;
    }

    .header .logo {
        width: 85px;
        margin-left: 5px;
    }

    .header .score span:nth-child(1) {
        font-size: .625rem;
        font-weight: 500;
        color: var(--score-text);
    }

    .header .score span:nth-child(2) {
        font-size: 2.2rem;
        font-weight: 700;
        letter-spacing: -3px;
        color: var(--dark-text);
    }
}